      // console.log(this.titleList)
      localStorage.setItem("ClassicId", item.id);
      //   sessionStorage.setItem("ClassicLabel", this.titleList.title);
      this.$router.push({
        path: "/classic/details", //跳转的路由
        name: "classicDetails" //路由的name
      });
    },
    goVideo(item, i) {
      var _this = this;

      this.$router.push({
        path: "/video/details"
      });
      this.$store.commit("setVideo", item);
    }
  },
  mounted() {},
  watch: {
    searchResult: function(val) {}
  },
  computed: {
    searchResult() {
      return this.$store.state.searchResult;
    }
  },
  components: {}
};
</script>
